import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";

function SocialLinks() {
    return(<Card style={{backgroundColor: "#f1faee"}}>
         <Card.Header className={"listTitle"} style={{textAlign: "center"}}>Social Media</Card.Header>
        <br />
        <Row>
            <Col md={4}>
                <img className={"linkImgs"} src="data:image/png;base64," alt="Twitter"></img>
            </Col>
            <Col md={8}>
                <Button href="https://twitter.com/Skirkp18" variant="info" style={{height: "100px"}} block><h2 style={{marginTop: "20px"}}>Twitter</h2></Button>
            </Col>
        </Row>
        <br />
        <Row>
            <Col md={4}>
                <img className={"linkImgs"} src ="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Instagram_logo_2016.svg/1200px-Instagram_logo_2016.svg.png" alt="Instagram"></img>
            </Col>
            <Col md={8}>
                <Button href="https://www.instagram.com/Skirkp18" variant="warning" style={{height: "100px"}} block><h2 style={{marginTop: "20px"}}>Instagram</h2></Button>
            </Col>
        </Row>
    </Card>);
};

export default SocialLinks;