import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import "./style.css"

function LinksCard() {
    return(<Card style={{backgroundColor: "#f1faee"}}>
        <Card.Header className={"listTitle"} style={{textAlign: "center"}}>Professional Links</Card.Header>
        <br />
        <Row>
            <Col md={4}>
                <img className={"linkImgs"} src={"https://cdn3.iconfinder.com/data/icons/inficons/512/github.png"} alt="GitHub" />
            </Col>
            <Col md={8}>
            <Button href="https://github.com/Skirkp18" variant="dark" style={{width: "100%", height: "100px" }}><h2 style={{marginTop: "20px"}}>Github</h2></Button>
            </Col>
        </Row>
        <br />
        <Row>
            <Col md={4}>
            <img className={"linkImgs"} src={"data:image/png;base64,"} alt="LinkedIn" />
            </Col>
            <Col md={8}>
            <Button href="https://www.linkedin.com/in/sean-kirkpatrick/" variant="primary" style={{width: "100%", height: "100px"}}><h2 style={{marginTop: "20px"}}>LinkedIn</h2></Button>
            </Col>
        </Row>
    </Card>);
};

export default LinksCard;